.hero {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 35px;
}

.hero h1{
    margin-top: 250px;
    font-size: 90px;
    text-align: center;
    width: 80%;
    font-weight: 600;
}

.hero h1 span{
    background: linear-gradient(90deg, #003366, #006699);
    background-clip: text;
    color: transparent;
}

.hero p{
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
}

.hero-action{
    display: flex;
    align-items: center;
    gap: 25px;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 50px;
}

.hero-connect {
    padding: 25px 45px;
    background: linear-gradient(90deg, #003366, #006699);
    border-radius: 50px;
    cursor: pointer;
}

.hero-connect:hover {
    border: 2px solid white;
}

@media(max-width:768px) {
    .hero img{
        margin-top: 120px;
        width: 260px;
    }

    .hero h1{
        margin-top: 50px;
        width: 90%;
        font-size: 50px;
    }

    .hero p{
        width: 60%;
        font-size: 18px;
        line-height: 32px;
        margin: 10px 0px;
    }

    .hero-action {
        gap: 16px;
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 50px;
    }

    .hero-connect {
        padding: 20px 30px;
        border-radius: 40px;
    }
}